<template>
  <div class="container bg-container" id="listIndexPage" :class="{'mb60': showSelect}">
    <van-nav-bar
      left-arrow
      title="期权委托记录"
      right-text=""
      @click-left="$goBack"
      @click-right="showSelect = !showSelect" />
    <div class="list">
      <van-checkbox-group v-model="selectIds" ref="checkboxGroup">
      <div class="item" v-for="item in records">
        <div class="title flex">
          <van-checkbox name="1" checked-color="#FBA10C" v-if="showSelect"></van-checkbox>
          <div class="flex-item">BETH {{Number(item.busdPerBeth/Math.pow(10, 10)).toFixed(3)}}-{{item.isOpen ? Number(item.openPrice/Math.pow(10, 10)).toFixed(3) : '*****'}}BUSD</div>
          <div class="status" v-if="item.isOpen && item.openPrice != item.busdPerBeth" >
             <span class="c-2FBBA1" v-if="item.openPrice - item.busdPerBeth > 0">涨</span><span v-else class="c-D4566C">跌</span>
          </div>
          <div class="status c-D4566C" v-if="item.isOpen && item.openPrice == item.busdPerBeth">返回本金</div>
        </div>
        <div class="flex row">
          <div class="label">委托周期：</div>
          <div class="flex-item cont">{{item.openBlock - item.investBlock}}个区块({{item.minute}}分钟)</div>
        </div>
        <div class="flex row">
          <div class="label">委托金额：</div>
       <div class="flex-item cont">{{item.investAmount}}BUSD <span class="c-D4566C" v-if="item.investType == 0">看跌</span><span v-else class="c-2FBBA1">看涨</span></div>
        </div>
        <div class="flex row">
          <div class="label">资金变化：</div>
          <template v-if="item.isOpen">
          <div class="flex-item cont" v-if="item.income"><span class="c-2FBBA1">+{{item.income}}BUSD</span></div>
          <div class="flex-item cont" v-else-if="item.busdPerBeth == item.openPrice"><span class="c-2FBBA1">+{{item.investAmount}}BUSD</span></div>
          <div class="flex-item cont" v-if="item.windIncome"><span class="c-2FBBA1">+{{item.windIncome}}WIND</span></div>
          </template>
          <div class="flex-item cont" v-else><span class="c-2FBBA1">*******</span></div>
           <div class="btn" v-if="item.canOpen" @click="openOrder(item.id)">开奖</div>
          <div class="flex-item" v-if="!item.canOpen && !item.isOpen"><span class="c-2FBBA1">等待开奖</span></div>
        </div>
      </div>
      </van-checkbox-group>
    </div>
    <div class="footer-handle flex-between" v-if="showSelect">
      <van-checkbox @change="checkAllFun" v-model="checkAll" checked-color="#FBA10C">全选</van-checkbox>
      <van-button round type="primary" color="#FBA10C" @click="onSubmitLottery">确认开奖</van-button>
    </div>
  </div>
</template>
<script>
// import { Toast } from 'vant';
import { reactive, toRefs, computed, watch} from "vue";
import BSNAbi from "@/utils/BSNAbi";
import tokenAbi from "@/utils/abi";
import BSNDataAbi from "@/utils/BSNDataAbi";
import config from "@/utils/config";
import {useStore} from "vuex";
export default {
  setup () {
    const store = useStore()
    const data = reactive({
      selectIds: [],
      showSelect: false,
      checkboxGroup: null,
      checkAll:false,
      records: [],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      BsnContract: null,
      BusdContract: null,
      windTokenContract: null,
      BsnDataContract: null,
      checkAllFun: (value) => {
        console.log(data.checkAll)
        data.checkboxGroup.toggleAll(value);
      },
      onSubmitLottery: () => {
        let ids = data.selectIds.join(',')
        console.log(ids)
      },
      initContract: () => {
        data.BsnContract = new data.web3.eth.Contract(BSNAbi, config.BsnAddress)
        data.BusdContract = new data.web3.eth.Contract(tokenAbi, config.BusdAddress)
        data.BsnDataContract = new data.web3.eth.Contract(BSNDataAbi, config.BsnDataAddress)
        data.windTokenContract = new data.web3.eth.Contract(tokenAbi, config.windToken)
        data.initData()
      },
      initData: () => {
        data.getList()
      },
      getList: async () => {
        data.records = []
        let userInfo = await data.BsnDataContract.methods.getUserInfo(data.address).call();
        let block = await data.web3.eth.getBlockNumber()
        let decimals = await data.BusdContract.methods.decimals().call();
        let windeciams = await data.windTokenContract.methods.decimals().call();
        userInfo._recordIds.forEach(async (item) => {
          let order = await data.BsnDataContract.methods.getOrderById(item).call();
          let price = await data.BsnContract.methods.getBlockPrice(order.openBlock).call()
          let result = await data.BsnContract.methods.getWin(item).call()
          console.log(result)
          if (price > 0 &&  block >= order.openBlock && !order.isOpen) {
            order.canOpen = true;
          }
          if (price > 0 &&  block >= order.openBlock) {
            if (result.back > 0) {
              order.income = Number(result.back/Math.pow(10, decimals)).toFixed(2)
              order.windIncome = Number(result.winTokenAmount * 8/Math.pow(10, windeciams)/9).toFixed(2)
              order.isWin = true;
            } else {
              order.income = Number(result.back/Math.pow(10, windeciams)).toFixed(2)
              order.windIncome = Number(result.winTokenAmount * 8/Math.pow(10, windeciams)/9).toFixed(2)
              order.isWin = false;
            }
          }
          if (price <=0) {
            order.canOpen = false;
          }
          console.log(order)
          order.id = item;
          order.openPrice = price;
          order.investAmount =Number(order.investAmount / Math.pow(10, decimals)).toFixed(2)
          data.records.unshift(order)
          data.records = data.records.sort((a, b) => b.investTime - a.investTime);
        })
      },
      openOrder:async (id) => {
        await data.BsnContract.methods.openAward(id).send({from: data.address});
        data.getList()
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
#listIndexPage {
  .footer-handle {
    font-size: 15px;
    font-weight: bold;
    .van-button {
      height: 41px;
      line-height: 41px;
      font-size: 16px;
      font-weight: bold;
      padding: 0 33px;
    }
  }
  .van-checkbox {
    margin-right: 9px;
  }
}
</style>
<style lang="less" scoped>
.mb60 {
  margin-bottom: 84px !important;
}
.list {
  margin: 15px;
  border-radius: 25px;
  background: #fff;
  padding: 20px 15px;
  .item {
    background: #EEEAF4;
    border-radius: 13px;
    padding: 12px;
    color: #8F80BA;
    .row + .row {
      margin: 6px 0 0;
    }
    .title {
      font-weight: 800;
      font-size: 15px;
      color: #452A7A;
      margin: 2px 0 6px;
      .status {
        font-size: 14px;
        margin: 0 0 0 15px;
      }
    }
    .cont {
      word-break: break-all;
    }
    .btn {
      padding: 0 22px;
      height: 25px;
      line-height: 25px;
      color: #fff;
      background: #1FC7D4;
      border-radius: 13px;
      font-size: 12px;
      margin: -9px 0 0 15px;
    }
  }
  .item + .item {
    margin-top: 15px;
  }
}
.footer-handle {
  position: fixed;
  bottom: 0;
  padding: 5px 18px;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  max-width: 640px;
  background: #fff;
}
</style>
